import * as React from "react";
import EmailTrackedChange from "./EmailTrackedChange";

import './css/Margins.css';

export default function Email(props) {

    var trackedChangeComponents = []
    for (var i = 0; i< props.changes.length; i++) {
        trackedChangeComponents.push(<EmailTrackedChange trackedData={props.changes[i]}></EmailTrackedChange>)
    }

    return (
        <div>
            {trackedChangeComponents}
            <p className="mt-4" style={{color: "#808080"}}>This email was automatically generated by Saga</p>
        </div>
    );


}
