import * as React from "react";
import TaskpaneHeader from "./TaskpaneHeader";
import TrackedCell from "./TrackedCell";
import Progress from "./Progress";
import Email from "./Email";
import { PrimaryButton } from 'office-ui-fabric-react';

import { makeClique, createSheet, getSheetsWithNames } from "../../saga/utils";
import { diff, trackedOutputChanges } from "../../saga/diff"; 

import './css/Margins.css';
import './css/Colors.css';
import './css/Text.css';
import './css/Reset.css';
import './css/Dimensions.css';
import './css/App.css';

/* global Button, console, Excel, Header, Progress */


/*
  There is a list of bindings in state that looks like this:

  this.state = {
    "bindings": [
      {
        "bindingName": <bindingName>,
        "edits": [<list of edited cells that effected this>]
      }
    ]
    ...
  }

  When this list updates, we also update the diff list, which looks like:

  this.state = {
    ...
    "trackedCellData": {
      "allChanges": [<diff data>],
      "trackedOutputs": [< tracked output changes>]
    }
  }

  We always keep these things in sync.

*/


function setStartupBehavior() {
  console.log("setting startup behavior");
  Office.context.document.settings.set("Office.AutoShowTaskpaneWithDocument", true);
  Office.context.document.settings.saveAsync();
}

function getBindingName(name, sheetName, address) {
  return JSON.stringify({
    "name": name,
    "sheetName": sheetName,
    "address": address
  });
}


// Creates a new binding object for this range with the name as the address
async function createNewBinding(context, name, sheetName, address) {
  const sheet = context.workbook.worksheets.getItem(sheetName);
  const range = sheet.getRange(address);
  const bindingName = getBindingName(name, sheetName, address);
  const binding = context.workbook.bindings.add(range, "Range", bindingName);
  await context.sync();
  return binding;
}

async function newCheckpoint(context) {
  const sheets = await getSheetsWithNames(context);
  const sheetNames = sheets.map(sheet => sheet.name).filter(name => !name.startsWith("s&") & name !== "saga");
  const sagaSheet = sheets.find(sheet => sheet.name === "saga");

  let newSagaSheet;
  if (!sagaSheet) {
    /*
      If there is no saga sheet, we create one, and also set the behavior of 
      the taskpane to open when the app does (as to start tracking).

      We wait 10 seconds, to avoid a bug on setting startup behavior to soon
      after the initialization of the taskpane.
    */
    newSagaSheet = await createSheet(context, "saga", Excel.SheetVisibility.hidden);
    setTimeout(setStartupBehavior, 10000);
    if (!newSagaSheet) {
      console.error("Error: could not create Saga sheet for project, not creating checkpoint");
      return;
    }
  } else {
    newSagaSheet = sagaSheet;
    // We delete the last commit
    const lastCheckpointSheets = sheets.filter(sheet => sheet.name.startsWith("s&"));
    lastCheckpointSheets.forEach(sheet => sheet.delete());
    await context.sync();
  }

  // We just append a s& to the sheet names
  const newSheetNames = sheetNames.map(sheetName => "s&"+ sheetName) 
  await makeClique(context, sheetNames, newSheetNames, Excel.WorksheetPositionType.end, Excel.SheetVisibility.hidden);
}

async function doesSheetExist(context, sheetName) {
  const sheets = await getSheetsWithNames(context);
  return sheets.find(sheet => sheet.name === sheetName);
}

async function runNewCheckpoint() {
  try{
    await Excel.run(async (context) => {
      await newCheckpoint(context);

    });
  } catch (e) {
    console.log(e);
  }
}

export default class App extends React.Component {
  constructor(props, context) {
    super(props, context);
    this.state = {
      bindings: [],
      newBinding: "",
      trackedCellData: null,
      newTrackedCellName: "",
      newTrackedCellSheetName: "",
      newTrackedCellAddress: "",
      newTrackedCellDropdown: false,
      showEmail: false,
      showWarning: false
    };

    this.handleChange = this.handleChange.bind(this);
    this.handleAddTrackedDropdown = this.handleAddTrackedDropdown.bind(this);
    this.submitNewTrackedCellForm = this.submitNewTrackedCellForm.bind(this);
    this.handleFormInput = this.handleFormInput.bind(this);
    this.untrackOutput = this.untrackOutput.bind(this);
    this.startTrackingBindings = this.startTrackingBindings.bind(this);
    this.toggleEmail = this.toggleEmail.bind(this);
    this.updateComment = this.updateComment.bind(this);
    this.toggleWarningBox = this.toggleWarningBox.bind(this);
  }

  cleanup = async () => {
    try{
      await Excel.run(async (context) => {
        // Clean up the sheets
        const sheets = await getSheetsWithNames(context);
        sheets.slice(1).forEach(sheet => sheet.delete());
        sheets[0].name = "Sheet1";
        await context.sync();

        try {
          sheets[0].getUsedRange().clear("all");
          await context.sync();
        } catch (e) {
          console.log("Nothing to clear from sheet.");
        }
  
        // Clean up the bindings
        const numBindings = context.workbook.bindings.getCount();
        await context.sync();
        for (let i = 0; i < numBindings.value; i++) {
          // For each binding, we register a unique tracker
          const binding = context.workbook.bindings.getItemAt(i);
          binding.delete();
        }
        await context.sync();
      });
      let cleanState = {};
      Object.keys(this.state).forEach(key => {
        cleanState[key] = undefined
      })
      cleanState["bindings"] = [];
      cleanState["newBinding"] = "";


      this.setState(cleanState);
    } catch (e) {
      console.log(e);
    }
  }

  addEditToBinding = async (bindingName, editedCell) => {
    // Build the new bindings
    let newBindings = [];
    for (let i = 0; i < this.state.bindings.length; i++) {
      let binding = this.state.bindings[i];
      if (binding.bindingName === bindingName) {
        // Add the edit in this case
        binding.edits = binding.edits.concat(editedCell);
      }
      newBindings.push(binding);
    }

    // Then, call the diff functions, and set the state
    let trackedCellData = {};
    try {
      await Excel.run(async context => {
        // First, we setup it's change listeners
        trackedCellData["trackedOutputs"] = await trackedOutputChanges(context, newBindings);
        trackedCellData["allChanges"] = await diff(context);
      })
    } catch (e) {
      console.log(e);
    }

    this.setState({
      "bindings": newBindings,
      "trackedCellData": trackedCellData,
    })
  }


  startTrackingBindings = async (bindingNames) => {
    console.log("tracking", bindingNames)
    try {
      await Excel.run(async context => {
        // First, we setup it's change listeners

        for (let i = 0; i < bindingNames.length; i++) {
          const bindingName = bindingNames[i];
          const binding = context.workbook.bindings.getItem(bindingName);
          const changeHandler = this.createHandlerForBindingName(bindingName);
          console.log("adding change handler");
          binding.onDataChanged.add(changeHandler);
          await context.sync();
        }

        const newBindingObjs = bindingNames.map(bindingName => {
          return {
            "bindingName": bindingName,
            "edits": []
          };
        });
      
        const newBindings = this.state.bindings.concat(newBindingObjs)

        let trackedCellData = {}
        trackedCellData["trackedOutputs"] = await trackedOutputChanges(context, newBindings);
        trackedCellData["allChanges"] = await diff(context);

        this.setState({
          "bindings": newBindings,
          "trackedCellData": trackedCellData,
        })
      })
    } catch (e) {
      console.log(e);
    }
  }

  removeBinding = async (bindingName) => {
    // First, we build a new binding list
    let newBindings = [];
    for (let i = 0; i < this.state.bindings.length; i++) {
      let binding = this.state.bindings[i];
      if (binding.bindingName !== bindingName) {
        newBindings.push(binding);
      }
    }

    // Then, get the new diffs and delete the binding
    let trackedCellData = {};
    try {
      await Excel.run(async context => {
        const bindingToDelete = context.workbook.bindings.getItem(bindingName);
        bindingToDelete.delete();
        await context.sync();

        trackedCellData["trackedOutputs"] = await trackedOutputChanges(context, newBindings);
        trackedCellData["allChanges"] = await diff(context);
      })
    } catch (e) {
      console.log(e);
    }

    this.setState({
      "bindings": newBindings,
      "trackedCellData": trackedCellData,
    });
  }

  createHandlerForBindingName(bindingName) {

    const handler = async (event) => {
      try {
        await Excel.run(async (context) => {
          console.log("running change handler");
          var range = context.workbook.getSelectedRange();
          range.load("address");
          await context.sync();

          /* 
            We just guess the edit occured one above the current cell

            This doesn't work when:
              - Copy and paste
              - Move using arrow keys rather than enter
              - Click off using mouse

            We def need something better...
          */
          

          const [sheet, address] = range.address.split("!");
          const col = address.match(/[a-zA-Z]+/g)[0];
          let row = address.match(/[0-9]+/g)[0];
          row = parseInt(row) - 1
          const editAddress = `${sheet}!${col + row}`;

          this.addEditToBinding(bindingName, editAddress);
        });
      } catch (e) {
        console.log(e);
      }
    }
    return handler;
  }

  handleChange(event) {
    this.setState({newBinding: event.target.value});
  }


  async componentDidUpdate(prevProps) {
    if (!prevProps.isOfficeInitialized && this.props.isOfficeInitialized) {
      // As soon as office is initalized, we register the binding holders

      try {
        await Excel.run(async context => {

          const numBindings = context.workbook.bindings.getCount();
          await context.sync();
          let bindingNames = []
          for (let i = 0; i < numBindings.value; i++) {
            // For each binding, we register a unique tracker
            const binding = context.workbook.bindings.getItemAt(i);
            await context.sync(); // Load the id
            bindingNames.push(binding.id);
          }

          this.startTrackingBindings(bindingNames);

          // Make sure we're synced
          await context.sync();

          // If the taskpane has never been used, we use it
          const sagaSheet = context.workbook.worksheets.getItemOrNullObject("saga");
          sagaSheet.load("isNullObject");
          await context.sync();
          if (sagaSheet.isNullObject) {
            await newCheckpoint(context);
          }
        });
      } catch (error) {
        console.error(error);
      };
    }
  }

  handleFormInput = (e) => {
    let nam = e.target.name;
    let val = e.target.value;
    this.setState({[nam]: val});
  }

  updateComment(trackedOutputName, comment) {
    this.setState(prevState => {
      let trackedCellData = Object.assign({}, prevState.trackedCellData); 
      for (var i = 0; i < trackedCellData.trackedOutputs.length; i++) {
        if (trackedCellData.trackedOutputs[i].name === trackedOutputName) {
          trackedCellData.trackedOutputs[i].comment = comment;
          break;
        }
      }      
      return { trackedCellData };      
    })
  } 

  toggleEmail() {
    this.setState(showEmail => {
      return {showEmail: !this.state.showEmail}
    });
  }

  toggleWarningBox () {
    this.setState(showWarning => {
      return {showWarning: !this.state.showWarning}
    });
  }

  handleAddTrackedDropdown() {
    this.setState(newTrackedCellDropdown => {
      return {newTrackedCellDropdown: !this.state.newTrackedCellDropdown}
    });
  }
  

  submitNewTrackedCellForm = async (e) => {
    e.preventDefault();
    const name = this.state.newTrackedCellName;
    const sheetName = this.state.newTrackedCellSheetName;
    const address = this.state.newTrackedCellAddress;

    // check if valid address
    const addressPattern = /\b[a-zA-Z]{1,10}\d{1,15}\b/;
    if (!addressPattern.test(address)) {
      this.toggleWarningBox()
      return;
    }

    try {
      await Excel.run(async context => {
        // check if valid sheet
        const isSheet = await doesSheetExist(context, sheetName);
        if (isSheet) {
          // create new binding
          const binding = await createNewBinding(context, name, sheetName, address);
          await this.startTrackingBindings([binding.id]);
        } else {
          this.toggleWarningBox()
        }  
      })
    } catch (e) {
      console.log(e);
    }

    this.handleAddTrackedDropdown();

  }


  async untrackOutput(name, sheetName, address) {
    const bindingName = getBindingName(name, sheetName, address);
    this.removeBinding(bindingName);
  }
  
  render() {
    const { title, isOfficeInitialized } = this.props;

    if (!isOfficeInitialized) {
      return (
        <Progress title={title} logo="assets/logo-filled.png" message="Please sideload your addin to see app body." />
      );
    }

    let TrackedCellsComponents = [];
    if (this.state.trackedCellData && this.state.trackedCellData.trackedOutputs) {
      this.state.trackedCellData.trackedOutputs.forEach((trackedOutput) => {
        TrackedCellsComponents.push(
          <TrackedCell 
            data={trackedOutput}
            key={getBindingName(trackedOutput.name, trackedOutput.sheetName, trackedOutput.address)}
            untrack={() => this.untrackOutput(trackedOutput.name, trackedOutput.sheetName, trackedOutput.address)}
            updateComment={this.updateComment}
              />
        );
      });
    }
    
    if (!this.state.showEmail) {
      return (
        <div className="ml-2 mr-2">
          <TaskpaneHeader title={"Track important cells to understand why they change"}></TaskpaneHeader>  
          <div className="section-header mt-2 mb-0"> 
            <p className="txt-20 txt-n">
              Tracked Outputs
            </p>
            <div onClick={this.handleAddTrackedDropdown}>
              {this.state.newTrackedCellDropdown ?
               <img className="mr-3" src="assets/minus.png" alt="plus" width="15" height="5"/> :
               <img className="mr-3" src="assets/plus.png" alt="plus" width="15" height="15"/> 
              }
            </div>
          </div>

          {this.state.showWarning && 
            <div className="warning-div ml-2">
              <p><b>Warning: </b>The cell you are trying to track does not exist. </p>
              <p className="mt-1">Make sure the sheet exists and the cell is formatted correctly eg: D55</p>
              <div className="warning-box-button-div mt-1 w-70 mr-0">
                <button className="warning-box-button w-60" type="button" onClick={(e) => this.toggleWarningBox(e)}>close</button>
              </div>
            </div>
          }
  
          {this.state.newTrackedCellDropdown &&
            <div className="mt-2">
              <p className="txt-16">Track a new cell</p>
              <form> 
                <div className="new-tracked-cell-inputs mt-1">
                  <div className="form-input">
                    <label className="w-20" htmlFor="newTrackedCellName">Name: </label>
                    <input className="w-40" type="text" name="newTrackedCellName" value={this.state.newTrackedCellName} placeholder="COGS 2021" onChange={this.handleFormInput} required/>
                  </div>
                  <div className="form-input">
                    <label className="w-20" htmlFor="newTrackedCellSheetName">Sheet: </label>
                    <input className="w-40" type="text" name="newTrackedCellSheetName" value={this.state.newTrackedCellSheetName} placeholder="Balance-Sheet" onChange={this.handleFormInput} required/>
                  </div>
                  <div className="form-input">
                    <label className="w-20" htmlFor="newTrackedCellAddress">Address: </label>
                    <input className="w-40" type="text" name="newTrackedCellAddress" value={this.state.newTrackedCellAddress} placeholder="D55" onChange={this.handleFormInput} required/>
                  </div>
                </div>
                <div className="new-tracked-cell-buttons mt-1">
                  <button className="ma-0" value="Submit" onClick={this.submitNewTrackedCellForm}>Start Tracking</button>
                </div>
              </form>
            </div> 
          }
          
          <div className="mt-1">
            <hr></hr>
            {TrackedCellsComponents.length !== 0 
              ? TrackedCellsComponents
              : <p>Add a tracked cell to understand how it evolves.</p>
              
              
            }
          </div>
          
          <button className={"mt-4 ml-2 mr-2 w-90"} onClick={this.toggleEmail}>
            Generate Summary Email
          </button>
        </div>
      );
    }

    if (this.state.showEmail) {
      return (
        <div> 
          <TaskpaneHeader title={"Copy and paste the message into your email"}></TaskpaneHeader>
          <Email name={this.state.name} changes={this.state.trackedCellData.trackedOutputs}></Email>
          <button className={"mt-4 ml-2 mr-2 w-90"} onClick={this.toggleEmail}>
              Return to Tracked Ouputs
          </button>
        </div>
      );
    }
  }
}
