import * as React from "react";
import EmailDirectChange from "./EmailDirectChange"
import './css/Margins.css';

export default function EmailTrackedChangeText(props) {

    var emailDirectChangeComponents = []
    for (var i = 0; i< props.trackedData.changedInputs.length; i++) {
        emailDirectChangeComponents.push(<EmailDirectChange directEdit={props.trackedData.changedInputs[i]}></EmailDirectChange>)
    }

    return (
        <div className="mt-2">
            <p className="mb-2"> 
                {props.trackedData.name} was changed from {props.trackedData.oldValue} to {props.trackedData.newValue}{props.trackedData.comment && " because " + props.trackedData.comment}.  
                {emailDirectChangeComponents.length > 0 && " " + props.trackedData.name + " was updated because I changed:"}
            </p>
            {emailDirectChangeComponents}
        </div>
    );
}